<template>

  <div class="pub-guess" ref="wrapper" :style="{height: height-44 + 'px'}" >
    <div class="guess-content" :style="{height: height-89 + 'px'}">
      <div class="part1">
        <textarea v-model="desc" :maxlength="textMax" class="desc-input" :class="{'desc-warn': descAcount}" placeholder="给你的竞猜想一个6个字以上的好玩的描述吧~要记得说明是猜什么哦~">
        </textarea>
        <div class="text-wrapper">
          <span class="text-count" :class="{'text-warn': descAcount}">{{desc.length}}</span>
          <span class="text-center">/</span>
          <span class="text-max">{{textMax}}</span>
        </div>
        <div class="btns-confirm">

          <div icon="Back" class="btn-add-resultimg">
            <img v-show="!show0" src=""  ref="img0" class="img-show">
            <p v-show="show0">答案图</p>
            <img v-show="show0" src="../../../static/guess/fabu_button02.png" class="img-upload">
            <vue-core-image-upload class="image-input"
                                   @imagechanged="imageChanged1"
                                   :isXhr="false">
            </vue-core-image-upload>
            <!--<input id="input0" type="file" class="image-input" accept="image/gif,image/jpeg,image/jpg,image/png,image/svg">-->
          </div>

          <div icon="Back" class="btn-add-refferimg">
            <img v-show="!show1" src=""  ref="img1" class="img-show">
            <p v-show="show1">参考图</p>
            <img v-show="show1" src="../../../static/guess/fabu_button01.png" class="img-upload">
            <vue-core-image-upload class="image-input"
                                   @imagechanged="imageChanged2"
                                   :isXhr="false">
            </vue-core-image-upload>
          </div>

          <div icon="Back" class="btn-add-refferimg">
            <img v-show="!show2" src=""  ref="img2" class="img-show">
            <p v-show="show2">参考图</p>
            <img v-show="show2" src="../../../static/guess/fabu_button01.png" class="img-upload">
            <vue-core-image-upload class="image-input"
                                   @imagechanged="imageChanged3"
                                   :isXhr="false">
            </vue-core-image-upload>
          </div>

          <div icon="Back" class="btn-add-refferimg">
            <img v-show="!show3" src=""  ref="img3" class="img-show">
            <p v-show="show3">参考图</p>
            <img v-show="show3" src="../../../static/guess/fabu_button01.png" class="img-upload">
            <vue-core-image-upload class="image-input"
                                   @imagechanged="imageChanged4"
                                   :isXhr="false">
            </vue-core-image-upload>
          </div>

        </div>
      </div>
      <div class="part2">
        <div class="group">
          <input type="text" required class="result-input-true" v-model="inputRight" maxlength="6">
          <label>填写正确答案</label>
        </div>
        <div class="group">
          <input type="text" required class="result-input-false" v-model="inputWrong" maxlength="6">
          <label>填写错误答案</label>
        </div>
      </div>
      <div class="part3">
        <div class="notice-title">发布须知</div>
        <div class="notice-content">
          1、答案图发布后将会被模糊化处理<br>
          2、多张描述图可以吸引更多人参与<br>
          3、竞猜结束时间默认为24小时或参与人数已满<br>
          4、猜一猜为社交娱乐平台，禁止发布非法赌博内容
        </div>
      </div>
    </div>
    <f7-button class="btn-next-step" @click="nextStep">下一步</f7-button>
  </div>

</template>

<script text="script/javascript">

  import coreImageUpload from 'vue-core-image-upload'
  import * as ImageCompress from '../../utils/canvas-compress'
  import EXIF from 'exif-js'
  import { mapState, mapActions } from 'vuex'

  export default {
    name: 'pub-guess',
    props: ['topBar'],
    data () {
      return {
        // 图片列表,
        fileReader: null,
        currReaderIndex: 0,
        orientation: 1000,
        show0: true,
        show1: true,
        show2: true,
        show3: true,
        imgUrl0: '',
        desc: '',
        inputRight: '',
        inputWrong: '',

        // 最多发布竞猜文字
        textMax: 40,
        textMin: 6,
        height: 0
      }
    },
    components: {
      'vue-core-image-upload': coreImageUpload
    },
    computed: {
      ...mapState({
        reset: state => state.uploadData.reset,
        token: state => state.token,
        shake: state => state.uploadData.shake,
        status: state => state.uploadData.status
      }),
      descAcount () {
        /* if (this.desc.length >= 90) {
          return true
        } else {
          return false
        } */
        return this.desc.length >= 30 ? 'true' : false
      }
    },
    methods: {
      ...mapActions([
        'assignImages'
      ]),
      init () {
        console.log('pubguess init')
      },
      nextStep () {
//        this.assignImages(this.images)
//        let textInput = document.getElementsByClassName('desc-input').item(0)
//        let trueInput = document.getElementsByClassName('result-input-true').item(0)
//        let falseInout = document.getElementsByClassName('result-input-false').item(0)
        // 默认值为10通过
        if (this.token !== null) {
          var isNext = 10
          if (this.desc.trim().length < this.textMin) {
            isNext = 1
          } else if (this.show0) {
            isNext = 2
          } else if (this.show1 && this.show2 && this.show3) {
            isNext = 3
          } else if (this.inputRight.trim().length === 0) {
            isNext = 4
          } else if (this.inputWrong.trim().length === 0) {
            isNext = 5
          } else if (this.inputWrong.trim() === this.inputRight.trim()) {
            isNext = 6
          }
          console.log(isNext)
          if (isNext === 1) {
            this.$f7.alert('竞猜描述字数请大于' + this.textMin + '个', '友情提醒')
          } else if (isNext === 2) {
            this.$f7.alert('请上传答案图', '友情提醒')
          } else if (isNext === 3) {
            this.$f7.alert('请上传参考图', '友情提醒')
          } else if (isNext === 4) {
            this.$f7.alert('请填写正确答案', '友情提醒')
          } else if (isNext === 5) {
            this.$f7.alert('请填写错误答案', '友情提醒')
          } else if (isNext === 6) {
            this.$f7.alert('正确答案和错误答案不能一致', '友情提醒')
          } else if (isNext === 10) {
            console.log('下一步了', isNext)
            this.$store.state.uploadData.desc = this.desc
            this.$store.state.uploadData.trueAnswer = this.inputRight
            this.$store.state.uploadData.falseAnswer = this.inputWrong
            this.$f7.views.postPop.router.load({url: '/pub-guess-set/'})
          }
        } else {
          this.$f7.popup('#login-choose')
        } 
        // this.$f7.views.postPop.router.load({url: '/pub-guess-set/'})
      },
      // todo 控制成按顺序导入图片，如果第一个没传 后面是禁用状态，如果可以删除的时候也必须从最后一个删除起
      imageChanged1 (payload) {
        this.fileReader.readAsDataURL(payload)
        this.currReaderIndex = 0 
      },
      imageChanged2 (payload) {
        this.fileReader.readAsDataURL(payload)
        this.currReaderIndex = 1
      },
      imageChanged3 (payload) {
        this.fileReader.readAsDataURL(payload)
        this.currReaderIndex = 2
      },
      imageChanged4 (payload) {
        this.fileReader.readAsDataURL(payload)
        this.currReaderIndex = 3
      },
      getOrientation (file) {
        EXIF.getData(file, function () {
          console.log('获取方向角了', EXIF.getTag(this, 'Orientation'))
          this.orientation = EXIF.getTag(this, 'Orientation')
        })
      }
    },
    mounted () {
      // 高度自适应满屏
      // :style="{height: height-44 + 'px'}"
      var height = document.documentElement.clientHeight
      this.height = parseInt(height)
      // this.$refs.wrapper.style.height = (this.height - 44) + 'px'

      // 上传图片按钮满框
      var aForm = document.getElementsByClassName('g-core-image-upload-form')
      console.log('aForm.length', aForm.length)
      for (let i = 0; i < aForm.length; i++) {
        aForm[i].style.cssText = 'height: 84px !important;'
      }
    },
    watch: {
      reset: {
        handler: function (val) {
          let self = this
          console.log('pub guess reset')
          /* this.desc = ''
          this.show0 = true
          this.show1 = true
          this.show2 = true
          this.show3 = true
          this.inputRight = ''
          this.inputWrong = ''
          self.$refs.img0.src = ''
          self.$refs.img1.src = ''
          self.$refs.img2.src = ''
          self.$refs.img3.src = '' */ 

          this.fileReader = new FileReader()
          this.fileReader.onloadend = function () {
            switch (self.currReaderIndex) {
              case 0:
                let oImg0 = self.$refs.img0
                oImg0.src = self.fileReader.result
                oImg0.onload = function () {
                  if (oImg0.width > oImg0.height) {
                    oImg0.style.cssText = 'height: 2.24rem'
                    // self.orientation = 'height: 2.24rem'
                  } else {
                    oImg0.style.cssText = 'width: 2.24rem'
                    // self.orientation = 'width: 2.24rem'
                  }
                } 
                let img0 = document.createElement('img')
                img0.onload = function () {
                  self.$store.state.uploadData.uploadImages[0] = ImageCompress.getBlobImage(this, 0.5, 1.0)
                  /* // img显示无横屏问题，但是从后端获取的图片也用img显示，就有横屏问题
                  let canvas = document.createElement('canvas')
                  let ctx = canvas.getContext('2d')
                  ctx.drawImage(this, 0, 0, 240, 240)
                  let dataUrl
                  dataUrl = canvas.toDataURL('image/jpeg')
                  oImg0.src = 'https://image.caiecai.net/headers/3449c9e5e332f1dbb81505cd739fbf3f.blob'
                  oImg0.src = dataUrl */
                }
                img0.src = self.fileReader.result
                self.show0 = false
                break
              case 1:
                let oImg1 = self.$refs.img1
                oImg1.src = self.fileReader.result
                oImg1.onload = function () {
                  if (oImg1.width > oImg1.height) {
                    oImg1.style.cssText = 'height: 2.24rem'
                  } else {
                    oImg1.style.cssText = 'width: 2.24rem'
                  }
                }
                let img1 = document.createElement('img')
                img1.onload = function () {
                  self.$store.state.uploadData.uploadImages[1] = ImageCompress.getBlobImage(this, 0.5, 1.0)
                }
                img1.src = self.fileReader.result
                self.show1 = false
                break
              case 2:
                let oImg2 = self.$refs.img2
                oImg2.src = self.fileReader.result
                oImg2.onload = function () {
                  if (oImg2.width > oImg2.height) {
                    oImg2.style.cssText = 'height: 2.24rem'
                  } else {
                    oImg2.style.cssText = 'width: 2.24rem'
                  }
                }
                let img2 = document.createElement('img')
                img2.onload = function () {
                  self.$store.state.uploadData.uploadImages[2] = ImageCompress.getBlobImage(this, 0.5, 1.0)
                }
                img2.src = self.fileReader.result
                self.show2 = false
                break
              case 3:
                let oImg3 = self.$refs.img3
                oImg3.src = self.fileReader.result
                oImg3.onload = function () {
                  if (oImg3.width > oImg3.height) {
                    oImg3.style.cssText = 'height: 2.24rem'
                  } else {
                    oImg3.style.cssText = 'width: 2.24rem'
                  }
                }
                let img3 = document.createElement('img')
                img3.onload = function () {
                  self.$store.state.uploadData.uploadImages[3] = ImageCompress.getBlobImage(this, 0.5, 1.0)
                }
                img3.src = self.fileReader.result
                self.show3 = false
                break
            }
          } 
        }
      },
      shake: {
        handler: function (val) {
          // 发布成功，此组件state数据清空
          if (this.status === null) {
            this.desc = ''
            this.show0 = true
            this.show1 = true
            this.show2 = true
            this.show3 = true
            this.inputRight = ''
            this.inputWrong = ''
            self.$refs.img0.src = ''
            self.$refs.img1.src = ''
            self.$refs.img2.src = ''
            self.$refs.img3.src = ''
          }
        }
      } 
    }
  }

</script>

<style scoped>

  .pub-guess {
    display: flex;
    display: -webkit-flex;
    flex-direction: column;
  }

  .guess-content {
    //flex: 1;
    overflow: auto;
  }

  p {
    margin-top: 0.2667rem;
    color: gray;
  }

  .result-input-true {
    /*margin-left: 15px;*/
    /*margin-top: -13px;*/
    /*border: 0px solid;*/
    /*background-color: gold;*/
    /*font-size: 16px;*/
  }

  .result-input-false {
    /*margin-left: 15px;*/
    /*margin-top: -13px;*/
    /*border: 0px solid;*/
    /*background-color: gold;*/
    /*font-size: 16px;*/
  }

  .image-input {
    position: absolute;
    /*background-color: aqua;*/
    opacity: 0;
    margin-top: 0px;
    width: 100%;
    height: 84px;
    top: 0px;
  }

  .uploaded-img {
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: transparent;
    border-radius: 5px;
    border: 0;
    margin: 0;
    z-index: 1000;
  }

  .part1 {
    margin-top: 10px;
    background-color: white;
    position:relative;
  }

  .part2 {
    margin-top: 10px;
    height: 64px;
    width: 100%;
    background-color: white;
    display: flex;
    display: -webkit-flex;
    justify-content: space-around;
    align-items: center;
  }

  .btns-confirm {
    margin-left: 2px;
    margin-right: 2px;
    display: flex;
    display: -webkit-flex;
    justify-content: space-around;
  }

  .btn-add-resultimg, .btn-add-refferimg{
    width: 2.24rem;
    height: 2.24rem;
    display: flex;
    display: -webkit-flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    border-radius: 5px;
    margin-bottom: 10px;
    position: relative;
    overflow: hidden;
  }

  div.btn-add-resultimg {
    border: 1px solid gold;
  }

  div.btn-add-refferimg {
    border: 1px solid darkgray;
  }

  .img-upload{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%);
    margin-top: -12px;
    width: 35px;
    height: 35px;
  }

  .btn-add-refferimg p,.btn-add-resultimg p{
    color:#999;
  }

  .desc-input {
    background-color: #f6f6f6;
    border-width: 0;
    border-radius: 6px;
    border:1px solid transparent;
    width: 90%;
    max-width: 90%;
    padding: 12px 12px;
    height: 120px;
    max-height: 120px;
    /*font-size: small;*/
    margin-top: 10px;
    overflow: hidden;
    resize: none;
    font-size: 14px;
  }

  .desc-warn{
    border: 1px solid #ff3c3c;
  }

  .text-wrapper{
    position:absolute;
    right:16px;
    top:130px;
  }

  .text-count{
    color:#999;
    font-size: 12px;
  }

  span.text-warn{
    color:#ff3c3c;
  }

  .text-center{
    font-size: 16px;
    color:#999;
    vertical-align: middle;
    margin:2px -2px 0;
  }

  .text-max{
    color:#999;
    font-size: 12px;
  }

  .btn-wrapper{
    background-color: gold;
  }

  .btn-next-step {
    height: 45px;
    width: 100%;
    color: black;
    background-color: gold;
    padding-bottom: 0px;
    border-width: 0px;
    border-radius: 0px;
    font-size: large;
    display: flex;
    display: -webkit-flex;
    align-items: center;
    justify-content: center;
  }

  .group {
    position:relative;
    margin-top: 50px;
    margin-bottom:50px;
    background: gold;
    border-radius: 4px;
    width:44%;
    /* width:165px; */
  }
  input {
    font-size:14px;
    color:#333;
    text-align: left;
    padding:26px 10px 10px 20px;
    display:block;
    width:100%;
    box-sizing: border-box;
    border:none;
    background: none;
  }
  input:focus {
    outline:none;
    padding:26px 10px 10px 20px;
  }
  label {
    color:#333;
    font-size:14px;
    font-weight:normal;
    position:absolute;
    pointer-events:none;
    left:50%;
    top:50%;
    transform: translateY(-50%);
    -moz-transform: translateY(-50%);
    -webkit-transform: translateY(-50%);
    transition:0.2s ease all;
    -moz-transition:0.2s ease all;
    -webkit-transition:0.2s ease all;
    width:100%;
    text-align:center;
    margin-left: -50%;
  }

  /* active state */
  input:focus ~ label, input:valid ~ label 		{
    top:30%;
    left: 50px;
    font-size:10px;
    color:#666;
  }

  .notice-title{
    font-size:14px;
    color:#666;
    margin:12px 0 0 12px;
    text-align: left;
  }

  .notice-content{
    font-size:14px;
    color:#999;
    text-align: left;
    padding:4px 0 10px 12px;
  }

  .img-show{
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    margin: 0;
  }
  
</style>



